import React from "react";
import { useParams,useHistory } from "react-router-dom";
import  products  from "../assets/product.json";
import { Card,Col, Row  } from "antd";
import styled from "styled-components";
import { EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
const StyledRow = styled(Row)`
  display:flex;
  flex-wrap: wrap;
  justify-content:center;
`;
const { Meta } = Card;
function Component() {
    const params = useParams();
    const history = useHistory();
    const pageTransfer=(list)=>{
        const url= "/products/"+params.type+"/"+list

            history.push(url)
    }
    var type;
  for (var product in products) {
    if (product === params.type) {
      var childrenItem = [];
      var i=0;
      type=products[product]
      for (var list in products[product]) {
          i++;
        childrenItem.push(
            
            <Col >
          <Card activeTabKey={list}
            hoverable
            style={{ width: 240,margin:20 }}
            cover={
                <img
                alt="example"
                src={products[product][list].photoAddress}
                onClick={pageTransfer.bind(this,list)}
                
              />
            }
            actions={[
                <SettingOutlined key="setting" />,
                <EditOutlined key="edit" />,
                <EllipsisOutlined key="ellipsis" />,
              ]}
          >
            <Meta  title={products[product][list].name} description={<><div> ${products[product][list].name}</div><div>{products[product][list].description}</div></>} />
          </Card>
          </Col>
          
        );
      }
    }
  }
  return (
    <>
      <div className="site-card-wrapper"  >

      <StyledRow className="ant-center"gutter={16} >
        {childrenItem}
        </StyledRow>
  </div>
    </>
  );
}

export default Component;
